footer {
  background: linear-gradient(135deg, #1a1d2e 0%, #2d1b3d 50%, #1e2438 100%);
  border-top: 1px solid #6366f133;
  position: relative;
  overflow: hidden;
  margin-top: var(--space-2xl) !important;
  padding: var(--space-2xl) 0 !important;
}

footer:before {
  content: "";
  pointer-events: none;
  background: radial-gradient(#6366f126 0%, #8b5cf61a 30%, #0000 70%);
  width: 120%;
  height: 200%;
  animation: 30s ease-in-out infinite footer-aurora;
  position: absolute;
  top: -50%;
  left: -10%;
}

@keyframes footer-aurora {
  0%, 100% {
    opacity: .6;
    transform: translateX(0)translateY(0)rotate(0);
  }

  33% {
    opacity: .8;
    transform: translateX(30px)translateY(-20px)rotate(5deg);
  }

  66% {
    opacity: .7;
    transform: translateX(-20px)translateY(30px)rotate(-3deg);
  }
}

footer:after {
  content: "";
  pointer-events: none;
  opacity: .5;
  background-image: linear-gradient(#6366f108 1px, #0000 1px), linear-gradient(90deg, #6366f108 1px, #0000 1px);
  background-size: 50px 50px;
  position: absolute;
  inset: 0;
}

footer .container-fluid {
  z-index: 1;
  position: relative;
}

footer h5 {
  margin-bottom: var(--space-lg);
  padding-bottom: var(--space-md);
  border-bottom: 2px solid;
  border-image: var(--aurora-gradient-accent) 1;
  background: var(--aurora-gradient-accent);
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-size: 1.25rem;
  font-weight: 700;
  position: relative;
}

footer h5:after {
  content: "";
  background: var(--aurora-gradient-accent);
  width: 60px;
  height: 2px;
  position: absolute;
  bottom: -2px;
  left: 0;
  box-shadow: 0 0 10px #6366f180;
}

footer .col-lg-4 p {
  color: #fffc;
  text-shadow: 0 1px 2px #0000004d;
  font-size: 1rem;
  line-height: 1.8;
}

footer ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

footer ul li {
  margin-bottom: var(--space-md);
  transition: transform var(--transition-fast);
}

footer ul li:hover {
  transform: translateX(5px);
}

footer ul li a {
  color: #ffffffd9;
  align-items: center;
  gap: var(--space-sm);
  padding: var(--space-xs) 0;
  transition: all var(--transition-fast);
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

footer ul li a:before {
  content: "";
  background: var(--aurora-gradient-accent);
  width: 0;
  height: 2px;
  transition: width var(--transition-fast);
  position: absolute;
  top: 50%;
  left: -10px;
  transform: translateY(-50%);
}

footer ul li a:hover:before {
  width: 20px;
}

footer ul li a:hover {
  color: #fff;
  text-shadow: 0 0 10px #6366f199;
  padding-left: var(--space-md);
}

footer ul li a i {
  color: #6366f1cc;
  transition: all var(--transition-fast);
  font-size: 1.1rem;
}

footer ul li a:hover i {
  color: #8b5cf6;
  transform: scale(1.2);
}

footer .d-flex.gap-4 {
  gap: var(--space-lg) !important;
}

footer .d-flex.gap-4 a {
  color: #ffffffb3;
  transition: all var(--transition-base);
  border-radius: var(--radius-full);
  backdrop-filter: blur(10px);
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  justify-content: center;
  align-items: center;
  width: 56px;
  height: 56px;
  font-size: 2rem;
  display: flex;
  position: relative;
}

footer .d-flex.gap-4 a:before {
  content: "";
  border-radius: var(--radius-full);
  background: var(--aurora-gradient-accent);
  opacity: 0;
  transition: opacity var(--transition-base);
  z-index: -1;
  position: absolute;
  inset: -2px;
}

footer .d-flex.gap-4 a:hover:before {
  opacity: 1;
}

footer .d-flex.gap-4 a:hover {
  color: #fff;
  background: none;
  border-color: #0000;
  transform: translateY(-5px)scale(1.1);
  box-shadow: 0 10px 30px #6366f166, 0 0 40px #6366f14d;
}

footer .d-flex.gap-4 a i {
  z-index: 1;
  position: relative;
}

footer .d-flex.gap-4 a:first-child:hover {
  box-shadow: 0 10px 30px #3b82f680, 0 0 40px #3b82f666;
}

footer .d-flex.gap-4 a:nth-child(2):hover {
  box-shadow: 0 10px 30px #1da1f280, 0 0 40px #1da1f266;
}

footer .d-flex.gap-4 a:nth-child(3):hover {
  box-shadow: 0 10px 30px #8b5cf680, 0 0 40px #8b5cf666;
}

footer .text-center {
  margin-top: var(--space-2xl);
  padding-top: var(--space-xl);
  color: #fff9;
  z-index: 1;
  border-top: 1px solid #ffffff1a;
  font-size: .95rem;
  position: relative;
}

footer .text-center:after {
  content: "";
  background: linear-gradient(90deg, #0000, #6366f180, #0000);
  width: 200px;
  height: 1px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

@media (width <= 992px) {
  footer h5 {
    margin-bottom: var(--space-md);
    font-size: 1.1rem;
  }

  footer .col-lg-3 {
    margin-top: var(--space-lg);
  }

  footer .d-flex.gap-4 a {
    width: 48px;
    height: 48px;
    font-size: 1.5rem;
  }
}

@media (width <= 768px) {
  footer {
    padding: var(--space-xl) 0 !important;
  }

  footer:before {
    opacity: .4;
    animation: none;
  }

  footer .d-flex.gap-4 {
    justify-content: center;
    gap: var(--space-md) !important;
  }

  footer ul li a:hover {
    padding-left: 0;
    transform: none;
  }

  footer ul li:hover {
    transform: none;
  }
}

@media (prefers-reduced-motion: reduce) {
  footer:before {
    animation: none !important;
  }

  footer ul li, footer ul li a, footer .d-flex.gap-4 a {
    transition: none !important;
  }
}

footer a:focus-visible {
  outline-offset: 4px;
  border-radius: var(--radius-sm);
  outline: 3px solid #6366f1cc;
}

footer {
  position: relative;
}

footer:after {
  content: "";
  pointer-events: none;
  background: radial-gradient(circle at 0 100%, #6366f126, #0000 70%);
  width: 100px;
  height: 100px;
  position: absolute;
  bottom: 0;
  left: 0;
}

footer .container-fluid:before {
  content: "";
  pointer-events: none;
  background: radial-gradient(circle at 100% 0, #8b5cf61f, #0000 70%);
  width: 150px;
  height: 150px;
  position: absolute;
  top: 0;
  right: 0;
}

[data-theme="dark"] footer {
  background: linear-gradient(135deg, #0a0c1a 0%, #1a0a2e 50%, #0f1123 100%);
  border-top-color: #6366f14d;
}

[data-theme="dark"] footer:before {
  opacity: .4;
}

@media print {
  footer {
    color: #000 !important;
    background: #1a1d2e !important;
  }

  footer:before, footer:after {
    display: none !important;
  }

  footer a {
    color: #000 !important;
    text-decoration: underline !important;
  }
}
